
.schedule-container {/*임시 배경*/
    padding: 20px;
    border-radius: 10px;
    max-width: 400px;
    margin: 0 auto;
  }
  
  
  .time-block {/*시각*/
    display: block;
    width: 20%;
    color: #ffffff;
    font-weight: bold;
    font-size: 12px;
    position: relative;
    top: 30px;
    left:30px;
    padding:3px;
    font-family: 'Inter', sans-serif;
  }
  .time-block_Oclock {/*정각*/
    display: block;
    width: 20%;
    color: #ffffff;
    font-weight: bold;
    font-size: 17px;
    position: relative;
    top: 30px;
    left: 14px;
    padding: 5px;
    font-family: 'Inter', sans-serif;
  }
  
  .nav-button-white {/*이전 버튼*/
    background-color: #ffffff;
    color: #1B377E;
    font-size: 16px;
    padding: 10px 20px;
    margin: 5px;
    border: none;
    border-radius: 20px;
    cursor: pointer;
    box-shadow: 2px 2px 7px rgb(201, 199, 199);
    position: relative;
    top: 10px;
    left: 10px;
  }
  .nav-button-navy {/*처음으로 버튼*/
    background-color: #1B377E;
    color: #ffffff;
    font-size: 20px;
    width: 242px;
    height: 48px;
    padding: 10px 20px;
    margin: 5px;
    border: none;
    border-radius: 20px;
    cursor: pointer;
    box-shadow: 2px 2px 7px rgb(201, 199, 199);
    position: relative;
    top: 60px;
  left: 0px;
  }
  .base-box {/*타임테이블 바탕*/
    position: relative;
    display: flex;
    margin: 0 auto;
    flex-direction: column;
    border-radius: 20px;
    width: 364px;
    height: 631px;
    background-color: #585CBF28;
    z-index: 0;
    box-shadow: 3px 3px 7px rgb(196, 193, 193);
  }
  
  .list-box_opening {/*오프닝*/
