
  body {
   
    display: flex;
    place-items: center;
    min-width: 430px;
    min-height: 100vh;
  }
  
  .bouncing-blob {
    width: 32vw;
    aspect-ratio: 1;
    border-radius: 50%;
    will-change: transform;
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    transform-origin: left top;
  }
  
  .bouncing-blob--blue {
    background: #4783c7;
  }
  
  .bouncing-blob--white {
    background: #ffffff;
    z-index: 2;
    width: 15vw;
  }
  
  .bouncing-blob--purple {
    background: #8c8ff1;
  }
  
  .bouncing-blob--pink {
    background: #e289cd50;
  }
  
  .bouncing-blobs-container {
    position: fixed;
    z-index: -1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  
  .bouncing-blobs-glass {
    position: absolute;
    z-index: 2;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    backdrop-filter: blur(140px);
    -webkit-backdrop-filter: blur(140px);
    pointer-events: none;
  }
  
  .bouncing-blobs {
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  
  .hero {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
  }
  
  .hero h2 {
    font-size: 70px;
    max-width: 800px;
    line-height: 1;
    text-align: center;
    font-weight: bold;
    text-transform: capitalize;
    margin-bottom: 20px;
    color: #021123;
  }
  
  .hero p {
    font-size: 24px;
    max-width: 500px;
    text-align: center;
    color: #021123;
  }
  
  @media (max-width: 1200px) {
    .bouncing-blobs-glass {
      backdrop-filter: blur(120px);
      -webkit-backdrop-filter: blur(120px);
    }
  }
  
  @media (max-width: 500px) {
    .bouncing-blob {
      width: 60vw;
    }
    .bouncing-blob--white {
      width: 30vw;
    }
    .bouncing-blobs-glass {
      backdrop-filter: blur(90px);
      -webkit-backdrop-filter: blur(90px);
    }
  }
  
  
  .vertical-buttons {
    display: flex;
    flex-direction: column;
    width: fit-content;
    height: 310px;
    margin-top: 50px;
    border-radius: 20px; 
    gap:12px;
  
  }
  
  .rectangle-1 {
    width: 430px;
    height: 114px;
    left: 1px;
    margin-top: 0px;
    border-radius: 0px 0px 62px 62px;
    background: #2851BC;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 0px 0px 62px 62px;
  
  }
  .rectangle-1-text{
    position: absolute;
    width: 85px;
    height: 48px;
    top: 70px;
    left: 176px;
    font-size: 20px;
    font-weight: 600;
    font-family: 'KCC-Ganpan';
    line-height: 24.2px;
    text-align: center;
    color: rgba(255, 255, 255, 1);
  
  }
  
  
  
  .rectangle-button {
    width: 364px;
    height: 90px; 
    margin-left: 32px;
    background: #fff; 
    border: 1px solid #ccc; 
    border-radius: 20px; 
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer; 
    box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.25);
  
    font-size: 16px;
    font-weight: 600;
    font-family: 'KCC-Ganpan';
    line-height: 19.36px;
    text-align: center;
    color: rgba(0, 0, 0, 1);
  }
  
  
  .rectangle-button:hover{
    background: #F4FF79D4;
    width: 364px;
    height: 90px; 
    margin-left: 32px;
    box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.25);
  }
  
  
  .rectangle-7 {
    position: absolute;
    width: 242px;
    height: 48px;
    top: 836px;
    left: 84px;
    gap: 0;
    border: 1px solid transparent;
    opacity: 0;
  }
  
  .modal {
    position: fixed;
    top: 160px;
    width: 430px; 
    height: 404px; 
    background-color: rgba(0, 0, 0, 0.5); 
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: white;
    border-radius: 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  }
  .modal-content {
    display: flex;
    flex-direction: column; 
    justify-content: center; 
    align-items: center; 
  }
  
  .modal-head {
    font-family: Inter;
    font-size: 11px;
    font-weight: 600;
    line-height: 19.36px;
    text-align: center;
    color: rgba(12, 46, 43, 1);
    margin-bottom: 60px;
  
    }
  
  .underline{
    width: 208px;
    top: 208px;
    border: 2px solid rgba(40, 81, 188, 1)
  
    }
  
    .modal-body {
      margin-bottom: 150px;
      font-weight: bold;
      font-family: Inter;
      font-size: 14px;
      font-weight: 600;
      line-height: 16.94px;
      text-align: left;
      color: rgba(0, 0, 0, 1);
      line-height: 1.5;
  
    }
  
  
  .backbtn {
    border-radius: 20px; 
    width: 242px;
    height: 48px;
    top: 494px;
   
    background: #1B377E; 
    border: 1px solid #1B377E; 
    font-size: 17px;
    font-weight: 600;
    font-family: 'KCC-Ganpan';
    line-height: 24.2px;
    line-height: 48px; 
    text-align: center; 
    color:rgba(255, 255, 255, 1);
    box-shadow: 0px 4px 15px 0px rgba(0, 0, 0, 0.25);
    display: flex;
    justify-content: center; 
    align-items: center; 
  }
  
  .back-to-top {
    width: 141px;
    height: 48px;
    margin-left: 140px;
    background: #1B377E; 
    border: 1px solid #1B377E; 
    border-radius: 20px; 
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer; 
    margin-top: 50px;
    font-size: 18px;
    font-weight: 600;
    font-family: 'KCC-Ganpan';
    line-height: 24px;
    text-align: center;
    color:rgba(255, 255, 255, 1);
    
  }

    
  .link {
    text-decoration: none;
    color: #000; 
  }

  .modal-image {
    align-items: center;
    
    max-width: 100%;
    
    height: auto;
    margin-left: 60px;
    margin-top: 50px;
   
   
    
    
  }

  .c{
    text-align: center;
  }

  .text_red{
    color: red;

  }
  
  .text_blue{
    color: blue;

  }