  .fixed-svg {
    width: 430px; /* SVG의 너비를 100%로 설정하여 부모 요소에 맞게 크기를 조정합니다. */
    height: auto; /* 높이를 자동으로 조정하여 가로세로 비율을 유지합니다. */
  }


  .select {
    
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    gap: 0px; /* 박스 사이의 간격 설정 */
    padding: 20px;
    width: 314px;
    height: 235px;
    background: rgba(245, 245, 245, 0.6);
    backdrop-filter: blur(4px);
    border-radius: 20px;
    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.25);
    margin-bottom: 30px;
  }
  
  .info_text {
    /* Information */
    font-family: 'KCC-Ganpan';
    font-weight: 600;
    font-size: 16px;
    color: #ffffff;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    grid-column: span 2; /* 정보 텍스트가 2열에 걸쳐 표시되도록 설정 */
    margin-bottom: 20px;
    
  }
  
  .box_text {
    /* 공지사항 Notice */

    font-family: 'KCC-Ganpan';
    font-weight: 600;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
    color: #0C2E2B;
    width: 100%; /* 각 박스의 너비 설정 */
    height: 100px; /* 각 박스의 높이 설정 */
    display: block;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
    
   
    border-radius: 10px; /* 테두리의 곡률 설정 */
    
