body {
   
    display: flex;
    place-items: center;
    min-width: 430px;
    min-height: 100vh;
  }
  
  .bouncing-blob {
    width: 32vw;
    aspect-ratio: 1;
    border-radius: 50%;
    will-change: transform;
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    transform-origin: left top;
  }
  
  .bouncing-blob--blue {
    background: #4783c7;
  }
  
  .bouncing-blob--white {
    background: #ffffff;
    z-index: 2;
    width: 15vw;
  }
  
  .bouncing-blob--purple {
    background: #8c8ff1;
  }
  
  .bouncing-blob--pink {
    background: #e289cd50;
  }
  
  .bouncing-blobs-container {
    position: fixed;
    z-index: -1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  
  .bouncing-blobs-glass {
    position: absolute;
    z-index: 2;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    backdrop-filter: blur(140px);
    -webkit-backdrop-filter: blur(140px);
    pointer-events: none;
  }
  
  .bouncing-blobs {
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  
  .hero {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
  }
  
  .hero h2 {
    font-size: 70px;
    max-width: 800px;
    line-height: 1;
    text-align: center;
    font-weight: bold;
    text-transform: capitalize;
    margin-bottom: 20px;
    color: #021123;
  }
  
  .hero p {
    font-size: 24px;
    max-width: 500px;
    text-align: center;
    color: #021123;
  }
  
  @media (max-width: 1200px) {
    .bouncing-blobs-glass {
      backdrop-filter: blur(120px);
      -webkit-backdrop-filter: blur(120px);
    }
  }
  
  @media (max-width: 500px) {
    .bouncing-blob {
      width: 60vw;
    }
    .bouncing-blob--white {
      width: 30vw;
    }
    .bouncing-blobs-glass {
      backdrop-filter: blur(90px);
      -webkit-backdrop-filter: blur(90px);
    }
  }
  
  .tutorial-link a {
    display: flex;
    justify-content: center;
    align-items: center;
    color: black;
    font-size: 20px;
    text-decoration: none;
    font-weight: 600;
    gap: 0 5px;
    border: 2px solid;
    border-radius: 5px;
    padding: 4px 10px;
  }
  
    

.app-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
  }
  
  .container {
    position: relative;
    width: 430px;
    height: 932px;
    overflow: hidden;
  }
  
  .background {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .vectors {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-end;
  }
  
  .vector22, .vector23 {
    position: absolute;
  }
  
  .vector22 {
    z-index: 2; 
    bottom: 0;
  }
  
  .vector23 {
    z-index: 1;
    right: 0;
    bottom: 2.15em;
  }
  

  
  .box1 {
    width: 364px; 
    height: 231px; 
    top: 15vh; 
    left: 4.2vh; 
    
  }
  
  .box-content {
    position: absolute;
    top: 45%;
    left: 50%;
    transform: translate(-50%, -35%);
    text-align: left; 
    width: 85%; 
    
  }
  
  .icon {
    position: absolute;
    top: 50%;
    right: -2%;
    transform: translateY(-50%);
  }
  
  
  .sub-text {
    font-size: 1em;
    color: #8E8E8E;
    margin-top: 0.05em;
    margin-bottom: 0.05em; 
    text-align: left; 
  
  }
  .main-text {
    font-weight: bold;
    font-size: 1em; 
    color: #0C2E2B; 
    margin-bottom: 0.05em;
  }
  
  
  .box2 {
    width: 364px; 
    height:186px; 
    top: 55vh;
    left: 4.2vh; 
  }
  .back-button {
    z-index: 3;
    position: absolute;
    bottom: calc(90/ 932 * 100vh);
    left: 2vh; 

  
    width: calc(242 / 932 * 100vh);
    height: calc(48 / 932 * 100vh);
    background-color: #1B377E;
    font-style: Bold;
    color: white;
    border: none;
    border-radius: 50px; 
    font-size: 1em;
  
    justify-content: center;
    align-items: center;
    box-shadow: 0px 8px 10px rgba(0, 0, 0, 0.3);
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer; 
    margin-top: 50px;
    font-size: 20px;
    font-weight: 600;
    font-family: 'KCC-Ganpan';
    line-height: 24px;
    text-align: center;
    color:rgba(255, 255, 255, 1);
    width: 242px;
    height: 48px;
  }
  
