.app-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 430px;
    height: 100vh;
}

.container {
    position: relative;
    width: 430px;
    height: 932px;
    overflow: hidden;
}



.street-table {
    
    position: absolute;
    
    transform: translate(-50%, -50%);
}

.prev-button {
    position: absolute;
    bottom: 150px; 
    left: 3vh; 
    width: 104px;
    height: 48px;
    background-color: rgba(245, 245, 245, 0.83);
    color: #0C2E2B;
    border: none;
    border-radius: 50px;
    font-size: 0.9em; 
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    z-index: 4; 
    box-shadow: 0px 5px 5px rgba(0, 0, 0, 0.3);
}

.back-button {
    width: 141px;
    height: 48px;
    top: 530px;
    margin-left: 140px;
    background: #1B377E; 
    border: 1px solid #1B377E; 
    border-radius: 20px; 
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer; 
    margin-top: 100px;
    font-size: 18px;
    font-weight: 600;
    font-family: 'KCC-Ganpan';
    line-height: 24px;
    text-align: center;
    color:rgba(255, 255, 255, 1);
    
   
}


.stimg{
    margin-left: 50%;
}


.box {
    
    position: absolute;
    background-color: rgba(245, 245, 245, 0.6);
    border-radius: 20px;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.3);
    margin-bottom: 100px; 
  }